import React from "react";
import styled from "styled-components";
import logo from "../img/logo.jpg";

const Course = styled.div`
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto;
  background: #833ae1;
  h1 {
    text-transform: uppercase;
    font-size: 300%;
    color: white;
    margin: 0px;
    margin-top: 10px;
    @media (max-width: 320px) {
      grid-area: 1 / 1 / 2 / 2;
      font-size: 160%;
      text-align: center;
      margin: 10px;
    }
  }
  img {
    justify-self: end;
    width: 80px;
    height: 80px;
    @media (max-width: 320px) {
      display: none;
    }
  }
  p {
    place-self: center center;
    margin: 20px;
    text-align: center;
    text-transform: uppercase;
    font-size: ${props => (props.size ? "36pt" : "120%")};
    color: ${props => (props.color ? "black" : "white")};
    @media (max-width: 320px) {
      display: none;
    }
  }
  a {
    place-self: center center;
    grid-area: 2 / 1 / 3 / 2;
    display: block;
    width: 200px;
    height: 50px;
    background: yellow;
    border-radius: 30px;
    font-size: 120%;
    text-transform: uppercase;
    margin-bottom: 10px;
    text-align: center;
    line-height: 50px;
    box-sizing: border-box;
    text-decoration: none;
    color: black;
  }
`;

export default () => (
  <Course>
    <h1>Креативное программирование</h1>
    <img src={logo} alt="Алгоритмика" />
    <p>Курс для детей 8-12 лет</p>
    <a href='https://vk.com/app6013442_-182406657?form_id=5#form_id=5'>Записаться</a>
  </Course>
);
