import React from "react";
import styled from "styled-components";

const Footer = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: #833ae1;
  @media (max-width: 320px) {
    grid-template-columns: 1fr;
  }
  p {
    place-self: center center;
    text-transform: uppercase;
    font-size: 120%;
    color: white;
    text-align: center;
    @media (max-width: 320px) {
      display: none;
    }
  }
  a {
    place-self: center center;
    width: 200px;
    height: 50px;
    background: yellow;
    border-radius: 30px;
    font-size: 120%;
    text-transform: uppercase;
    margin: 10px;
    text-decoration: none;
    text-align: center;
    line-height: 50px;
    box-sizing: border-box;
  }
`;

export default () => (
  <Footer>
    <p>Записаться на бесплатный мастер класс</p>
    <a href='https://vk.com/app6013442_-182406657?form_id=5#form_id=5'>Записаться</a>
  </Footer>
);
