import React from "react";
import { ActionSheet, ActionSheetItem, IOS, View, Panel, CellButton } from "@vkontakte/vkui"
import { osname, Icon28Profile, Icon28CameraOutline } from "@vkontakte/vk-connect"
import Course from "./Course";
import Table from "./Table";
import Video from "./Video";
import Parents from "./Parents";
import Footer from "./Footer";

export default class Popout extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      popout: null
    }

    this.openBase = this.openBase.bind(this);
    this.openIcons = this.openIcons.bind(this);
    this.openThemes = this.openThemes.bind(this);
  }

  componentDidMount() {
    this.openBase();
  }

  openBase () {
    this.setState({ popout:
      <ActionSheet onClose={() => this.setState({ popout: null })}>
        <ActionSheetItem autoclose>
          По дням
        </ActionSheetItem>
        <ActionSheetItem autoclose>
          По неделям
        </ActionSheetItem>
        <ActionSheetItem autoclose>
          По месяцам
        </ActionSheetItem>
        {osname === IOS && <ActionSheetItem autoclose theme="cancel">Отменить</ActionSheetItem>}
      </ActionSheet>
    });
  }

  openIcons () {
    this.setState({ popout:
      <ActionSheet onClose={() => this.setState({ popout: null })}>
        <ActionSheetItem autoclose before={<Icon28Profile/>}>
          Редактировать профиль
        </ActionSheetItem>
        <ActionSheetItem autoclose before={<Icon28CameraOutline/>}>
          Изменить фотографию
        </ActionSheetItem>
        {osname === IOS && <ActionSheetItem autoclose theme="cancel">Отменить</ActionSheetItem>}
      </ActionSheet>
    });
  }

  openThemes () {
    this.setState({ popout:
      <ActionSheet onClose={() => this.setState({ popout: null })}>
        <ActionSheetItem autoclose>
          Редактировать
        </ActionSheetItem>
        <ActionSheetItem autoclose theme="destructive">
          Выйти
        </ActionSheetItem>
        {osname === IOS && <ActionSheetItem autoclose theme="cancel">Отменить</ActionSheetItem>}
      </ActionSheet>
    });
  }



  render() {
    return (
      <View popout={this.state.popout} header={false} activePanel="popout">
        <Panel id="popout">
          <CellButton onClick={this.openBase}>Базовый список</CellButton>
          <CellButton onClick={this.openIcons}>Список с иконками</CellButton>
          <CellButton onClick={this.openThemes}>Темы</CellButton>
        </Panel>
      </View>
    )
  }
}